.contribution-graph{

  td{
    font-size: 12px;
    padding: 0;
  }

  tr{
    border-spacing: 0px;
  }

  &-legend{
    margin-left: 20px;
    display: flex;
    column-gap: 2px;
    margin-top: 15px;
    align-items: center;

    & > span {
      font-size: 10px;

      &:first-child{
        margin-right: 10px;
      }

      &:last-child{
        margin-left: 10px;
      }
    }

  }
}