.contribution-graph-element {
  position: relative;
  width: 15px;
  height: 15px;

  button {
    width: 15px;
    height: 15px;
    background-color: #EDEDED;
    border: 1px;
    margin: 0;
    outline: none;
    border: none;
    padding: 0;
  }

  &:hover,
  &:active {
    button {
      border: 1px solid black;
    }
  }

  &.two {
    button {

      background-color: #ACD5F2;
    }
  }

  &.three {
    button {

      background-color: #7FA8C9;
    }
  }

  &.four {
    button {

      background-color: #527BA0;
    }
  }

  &.five {
    button {

      background-color: #254E77;
    }
  }

  &__info {
    position: absolute;
    bottom: 23px;
    right: calc(50% - 50px);

    color: #fff;
    background-color: black;
    border-radius: 3px;
    padding: 5px 9px 6px;

    display: flex;
    flex-direction: column;
    align-items: center;

    &::before {
      content: '';
      display: block;
      position: absolute;
      height: 8px;
      width: 8px;
      background-color: black;
      transform: rotate(45deg);
      bottom: -4px;
    }
  }

  &__contributions {
    font-weight: 400;
    font-size: 12px;
    white-space: nowrap;
  }

  &__date {
    font-weight: 400;
    font-size: 10px;
    color: #7C7C7C;
    white-space: nowrap;
  }
}